.clients-section{
    padding: 120px;
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
}

.clients-section-content h1{
    font-weight: 500;
    font-size: 40px;
    color: #000;
}

.clients-section-content p{
    max-width: 320px;
    margin-top: 20px;
    color: #757575;
    font-size: 18px;
    line-height: 28.8px;
}

.clients-section-logos{
    display: flex;
    gap: 60px;
}

.clients-section-logos img{
    align-self: center;
}


@media screen and (max-width: 1150px) {
    .clients-section{
        flex-direction: column;
        padding: 2rem;
        text-align: center;
    }

    .clients-section-logos{
        flex-direction: column;
        margin-top: 25px;
    }
}