.product-section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 120px;
    padding-top: 0;
}

.product-section-content h1{
    font-weight: bold;
    font-size: 40px;
    line-height: 49px;
}

.product-section-content p{
    font-weight: 400;
    font-size: 16px;
    color: #565656;
    line-height: 26px;
    margin-top: 30px;
}

.product-section-content{
    max-width: 500px;
}

.product-section-img{
    position: relative;
}

.product-section-img img:first-child{
    position: absolute;
    text-align: center;
    top: 40%;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
}

.product-section-img .ellipse{
    position: absolute;
    z-index: -1;
    right: 0;
    bottom: 15px;
}

.product-section-content{
    position: relative;
}

.product-section-content img{
    position: absolute;
    z-index: -1;
}

@media screen and (max-width: 1150px) {
    .product-section{
        flex-direction: column-reverse;
        padding: 2rem;
    }

    .product-section-img img{
        width: 100%;
    }
    
    .product-section-img img:first-child{
        width: 5%;
    }
}