.footer-section{
    padding: 120px;
    padding-bottom: 0;
}

.footer-section-layout{
    display: flex;
    gap: 120px;
    justify-content: space-between;
    padding-bottom: 60px;
}

.footer-block h4{
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    padding-bottom: 25px;
}

.footer-block p{
    color: #565656;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
}

.footer-block-links{
    display: flex;
    gap: 16px;
    margin-top: 18px;
}

.footer-block{
    max-width: 300px;
}

.footer-block ul li{
    list-style: none;
}

.footer-block ul li a{
    text-decoration: none;
    color: #000;
    font-weight: 400;
    font-size: 14px;
    line-height: 28px;
}

.flex-block-2{
    display: flex;
    gap: 120px;
}

.copyright{
    text-align: center;
    margin-top: 50px;
    padding-bottom: 20px;
}

.copyright p{
    color: #A4A4A4;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
}

.line{
    background: #E4E4E4;
    height: 1px;
    margin-bottom: 60px;
}


@media screen and (max-width: 1150px) {
    .footer-section-layout{
        flex-direction: column;
    }

    .flex-block-2{
        flex-direction: column;
    }
}