.cards-section{
    padding: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0;
    position: relative;
}

.cards-section .rectangle{
    position: absolute;
    z-index: -1;
}

.cards-section .bigrectangle{
    position: absolute;
    z-index: -1;
    right: 0;
}

.cards-section .dotornamentbig{
    position: absolute;
    bottom: 150px;
}

.cards-section .circle{
    position: absolute;
    right: 90px;
    bottom: 150px;
    z-index: -2;
}

.cards-section-cards-layout{
    display: flex;
    gap: 40px;
}

.cards-section-content h1{
    color: #000;
    line-height: 49px;
    font-size: 40px;
    font-weight: bold;
}

.cards-section-content p{
    color: #565656;
    font-size: 16px;
    margin-top: 30px;
    line-height: 26px;
    font-weight: 400;
}

.cards-section-content{
    max-width: 400px;
}

.cards-section-card{
    padding: 30px;
    border: 1px solid #F2F2F2;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.05);
    border-radius: 40px;
    text-align: center;
    max-width: 350px;
    background: #fff;
}

.cards-section-card h4{
    font-size: 24px;
    line-height: 29px;
    margin-top: 30px;
    padding-bottom: 30px;
}

.cards-section-cards{
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.cards-section-cards-layout div:first-child{
    margin-top: 70px;
}

@media screen and (max-width: 1150px) {
    .cards-section{
        padding: 0;
        flex-direction: column;
    }

    .cards-section-content{
        text-align: center;
    }

    .cards-section-cards-layout{
        flex-direction: column;
    }
    
    .cards-section-cards-layout div:first-child{
        margin-top: 30px;
    }

    .dotornamentbig{
        display: none;
    }
}