.hero-section{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.hero-section-content{
    padding: 120px;
}

.hero-section-content h1{
    font-size: 40px;
    font-weight: bold;
    color: #000;
    line-height: 49px;
}

.hero-section-content p{
    color: #565656;
    font-size: 16px;
    font-weight: 400;
    margin-top: 40px;
    padding-bottom: 33px;
    max-width: 500px;
    line-height: 26px;
}

.hero-section-content a{
    padding: 20px 40px;
    color: #fff;
    background: #2639ED;
    border-radius: 60px;
    text-decoration: none;
    font-weight: 500;
    font-size: 20px;
}

.hero-section-content{
    position: relative;
}
.hero-section-content img{
    position: absolute;
    left: 68px;
    z-index: -1;
}

@media screen and (max-width: 1150px) {
    .hero-section{
        flex-direction: column;
    }

    .hero-section-img img{
        width: 100%;
    }

    .hero-section-content{
        padding: 2rem;
        align-items: center;
        text-align: center;
    }

    .hero-section-content img{
        width: 0;
    }
}