.subscribe-block h1{
    font-weight: bold;
    font-size: 40px;
    line-height: 49px;
}

.subscribe-block p{
    color: #757575;
    font-weight: 400;
    font-size: 18px;
    line-height: 29px;
    margin-top: 20px;
}

.subscribe-block{
    padding: 100px;
    background: #F4F9FF;
    border-radius: 75px;
    display: flex;
    justify-content: center;
    gap: 10px;
    position: relative;
    max-height: 292px;
}

.subscribe-block-form input{
    padding: 30px;
    border: none;
    border-radius: 60px;
    color: #959595;
}

.subscribe-block-form button{
    background: #2639ED;
    padding: 20px 40px;
    color: #fff;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    border-radius: 60px;
    outline: none;
    cursor: pointer;
    border: none;
}

.subscribe-block-form{
    background: #fff;
    border-radius: 60px;
    display: flex;
    align-items: center;
    padding: 20px;
    gap: 15px;
    box-shadow: 10px 20px 50px rgba(0, 0, 0, 0.15);
}

.subscribe-section{
    padding: 120px;
    padding-top: 0;
    position: relative;
}

.subscribe-section .rectangle{
    position: absolute;
    bottom: 85px;
    z-index: -1;
    left: 100px;
}

.subscribebg{
    position: absolute;
    z-index: 0;
    right: 0;
    top: 0;
}

.subscribe-block-content, .subscribe-block-form{
    z-index: 1;
}

.subscribe-block-form input:focus{
    outline: none;
}

.dotornament{
    position: absolute;
    right: 20px;
    top: 0;
}

@media screen and (max-width: 1150px) {
    .subscribe-block{
        flex-direction: column;
        padding: 2rem;
        text-align: center;
    }
    
    .subscribe-section{
        padding: 2rem;
    }

    .subscribebg{
        display: none;
    }

    .subscribe-block-form{
        justify-content: center;
    }

}