.reviews-section-slider{
    display: flex;
    justify-content: space-between;
    gap: 50px;
    padding: 120px;
    padding-top: 0;
    align-items: center;
}

.reviews-section-slider .center h4{
    font-size: 24px;
    line-height: 29px;
    font-weight: 500;
}

.reviews-section-slider .center p{
    color: #565656;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    margin-top: 22px;
    padding-bottom: 14px;
}

.reviews-section-slider .center{
    max-width: 400px;
}

.dots{
    display: flex;
    gap: 26px;
}

.dots .dot{
    background: #E7F0FC;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    cursor: pointer;
}

.dots .active{
    width: 16px;
    height: 16px;
    background: #2639ED !important;
}

.left{
    position: relative;
}

.left .circle{
    position: absolute;
    bottom: 45px;
    left: 45px;
    z-index: -1;
}

.left .dotornament{
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
}

.reviews-section-header{
    text-align: center;
    padding-bottom: 100px;
}

.reviews-section-header h1{
    font-weight: bold;
    font-size: 40px;
    line-height: 49px;
    color: #000;
}

.reviews-section-header p{
    color: #757575;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    padding-top: 20px;
}

.right{
    position: relative;
}

.right .ellipse{
    position: absolute;
    z-index: -1;
    top: 120px;
    left: 45px;
}

@media screen and (max-width: 1150px) {
    .reviews-section-slider{
        flex-direction: column;
        padding: 2rem;
    }

    .reviews-section-slider img{
        width: 100%;
    }
}