.nav__menu a{
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    color: #000;
}

.nav__links{
    display: flex;
    gap: 50px;
}

.nav__links li{
    list-style: none;
}

.nav__menu{
    display: flex;
    justify-content: space-between;
    padding: 42px 120px;
}

.nav__logo a{
    font-size: 28px;
    font-weight: 400;
}

.nav__logo a span{
    font-weight: bold;
}

.nav__links-smallscreen-menu{
    display: none;
}

@media screen and (max-width: 1100px) {
    .nav__links{
        display: none;
    }

    .nav__links-smallscreen-menu{
        display: unset;
    }
    .nav__links-smallscreen_overlay{
        display: flex;
    }

    .nav__links-smallscreen li{
        list-style: none;
    }
    
    .nav__links-smallscreen a{
        text-decoration: none;
        color: #000;
    }

    .nav__links-smallscreen_overlay{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 2rem;
        text-align: center;
        z-index: 1000;
    }
    
    .nav__links-smallscreen li{
        margin-top: 2rem;
    }

    .overlay__close, .overlay__open{
        font-size: 27px;
        cursor: pointer;
        position: absolute;
        top: 30px;
        right: 20px;
    }

    .nav__buttons {
        display: none;
    }

    .nav__menu{
        padding: 1rem;
    }
}